
.read-local-file {
  position: absolute;
  z-index: 1000;
  top: 30px;
  bottom: 0;
  overflow: hidden;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  color: #ccc;
  text-align: center;
}
.read-local-file .info-wraper {
    position: absolute;
    top: 0;
    bottom: 0;
    display: table;
    width: 100%;
    margin: auto 0;
}
.read-local-file .info-wraper h3 {
      width: 100%;
      margin: auto;
}
.read-local-file .info-wraper .add-icon {
      font-size: 50px;
      color: #ccc;
}
.read-local-file .info-wraper .add-icon:hover {
        cursor: pointer;
        color: #17bd2b;
}
.read-local-file .info-wraper .add-icon input {
        display: none;
}
.read-local-file .file-type-tip {
    position: absolute;
    top: 1em;
    width: 100%;
    text-align: center;
    color: red;
}
.read-local-file .cancel-import {
    position: absolute;
    right: .2em;
    font-size: 30px;
    line-height: 1;
    color: #919191;
}
.read-local-file .cancel-import:hover {
      color: #c92828;
}



